@import url(https://fonts.googleapis.com/css?family=Noto+Sans+HK:400,700&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
* {
    font-family: 'Noto Sans HK', sans-serif;
}

html, body, #root {
    height: 100%;
    overflow: auto; 
    -webkit-overflow-scrolling: touch;
}

.main {
    min-height: 100%; 
    display: flex;
    align-items: center;
}

.title {
    margin: 5px auto 0px;
}

.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.button-pdf{
    width: 14em;
    height: 4em;
    color: white;
    text-align: center;
    background-color: #778899;
    border-radius: .3em ;
    border: none;
    display:block;
    margin:15px auto;
    padding: 5px;
  }

  .button-pdf:hover{
    text-decoration: none;
    color: white;
  }

  .fa-file-pdf-o{
    margin-left: 0.2em;
  font-size:24px
}

.mapa {
    margin-left: -16px;
    margin-top: 20px ;
}

.footer{
    margin-top: 1rem;
    padding: 4px;
    background-color: slategray;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height:60px;
    text-align: center;
    font-size: 1,5vw;
  }
